import React from "react"
import Layout from "../components/layout"
import Project from "../components/project"
import gameData from "../data/gameData.js"
import {Container} from 'react-bootstrap'

export default () => (
  <Layout>
    <Container>
      {gameData.map(project =>
          <Project 
          info={project}
          key={project.id}
          /> )
      }
    </Container>
  </Layout>
)